<template>
	<view class='wrapper padding'>
		<view class="form_wrapper">
			<view :class="['qx-from-group',fromType==2?'no_edit':'']">
				<view class="title">姓名<text class='text-red'>*</text></view>
				<input placeholder="请输入姓名" name="input" v-model="userInfo.name" :disabled="fromType==2"></input>
			</view>
			<view class="qx-from-group">
				<view class="title">性别<text class='text-red'>*</text></view>
				<view class="radio_group">
					<view class="radio_item" @click="switchSex(0)">
						<image src="../../static/select.png" v-if="userInfo.gender==0"></image>
						<image src="../../static/unselect.png" v-else></image>
						<text>先生</text>
					</view>
					<view class="radio_item" @click="switchSex(1)">
						<image src="../../static/select.png" v-if="userInfo.gender==1"></image>
						<image src="../../static/unselect.png" v-else></image>
						<text>女士</text>
					</view>
				</view>
			</view>
			<view class="qx-from-group">
				<view class="title">住址<text class='text-red'>*</text></view>
				<input placeholder="请输入住址" name="input" v-model="userInfo.address" type="text"></input>
			</view>
			<view :class="['qx-from-group no-border',fromType==2?'no_edit':'']">
				<view class="title">手机<text class='text-red'>*</text></view>
				<input 
					placeholder="请输入手机" 
					name="input" 
					type="number" 
					v-model="userInfo.mobile"
					:disabled="true"
				></input>
			</view>
		</view>
		<view class="form_wrapper">
			<view class="qx-from-group no-border">
				<view class="title">邮箱</view>
				<input placeholder="请输入邮箱（选填）" name="input" v-model="userInfo.email"></input>
			</view>
		</view>
		<view class="form_wrapper">
			<view class="qx-from-group">
				<view class="title">职业<text class='text-red'>*</text></view>
				<picker
					@change="careerChange" 
					:value="careerIndex" 
					:range="careerList" 
					class="picker"
					range-key="name"
				>
					<view :class="[!userInfo.career?'placeholder_style':'','picker']">
						{{careerIndex!=null? careerList[careerIndex].name : '请选择职业'}}
					</view>
				</picker>
				<image src="../../static/down_arrow.png" class="img"></image>
			</view>
			<view class="qx-from-group no-border">
				<view class="title">收入区间<text class='text-red'>*</text></view>
				<picker 
					@change="inComeChange" 
					:value="inComeIndex" 
					:range="inComeList" 
					class="picker"
					range-key="name"
				>
					<view :class="[!userInfo.incomeLevel?'placeholder_style':'','picker']">
						{{inComeIndex!=null? inComeList[inComeIndex].name : '请选择收入区间'}}
					</view>
				</picker>
				<image src="../../static/down_arrow.png" class="img"></image>
			</view>
		</view>
		<view class="register_btn">
			<button class="cu-btn block lg" :disabled="btnDisabled" @click="register">
				<text v-if="btnDisabled" class="cuIcon-loading2 cuIconfont-spin"></text> 
				{{this.fromType==1?"注册":"保存"}}
			</button>
		</view>
	</view>
</template>

<script>
	import {REG} from "../../common/Const.js"
	
	export default {
		data() {
			return {
				btnDisabled:false,
				fromType:1,// 1是注册，2是修改
				inComeList: [], //收入
				inComeIndex:null, //收入区间
				careerList:[],//职业
				careerIndex:null,//职业
				userInfo:{
					authId:'',	//用户授权后生成的记录id	Integer	是	
					type:3,	//人员所属类型	Integer	是	1-管理员  2-员工  3-用户
					mobile:'',	//手机号	String	是	
					name:'',	//姓名	String	是	
					address:'',	//地址	String	是	
					email:'',	//邮箱:	String	是	
					career:'',//	职业	Integer	是	
					incomeLevel:null,//	收入区间
					gender:0,//0 男，1女
					lng:'',
					lat:'',
					channelId:""// 渠道
				}
			};
		},
		methods:{
			inComeChange(e) {
				this.inComeIndex = e.detail.value
				this.userInfo.incomeLevel=this.inComeList[this.inComeIndex].code
			},
			careerChange(e){
				this.careerIndex = e.detail.value
				this.userInfo.career=this.careerList[this.careerIndex].code
			},
			
			switchSex(sex){
				this.userInfo.gender=sex
			},
			
			getInComeList(){
				this.request("/common/incomeLevels").then((res)=>{
					if(res.resultCode==0){
						this.inComeList=res.data
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常，请稍后重试',icon:'none'})
					}
				})
			},
			getCareerList(){
				this.request("/common/carceers").then((res)=>{
					if(res.resultCode==0){
						this.careerList=res.data
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常，请稍后重试',icon:'none'})
					}
				})
			},
			
			register(){
				if(!this.userInfo.name){ uni.showToast({title: '请输入用户名',icon:'none'});return}
				if(!this.userInfo.address){ uni.showToast({title: '请输入住址',icon:'none'});return}
				if(!this.userInfo.mobile){ uni.showToast({title: '请输入手机号',icon:'none'});return}
				if(this.userInfo.career==null){ uni.showToast({title: '请选择职业',icon:'none'});return}
				if(this.userInfo.incomeLevel==null){ uni.showToast({title: '请选择收入区间',icon:'none'});return}
				if(this.userInfo.email){
					if(!REG.Email.test(this.userInfo.email)){uni.showToast({title: '请填写正确的邮箱地址',icon:'none'});return}
				}
				this.btnDisabled=true
				if(this.fromType==1){
					this.request("/user/regsterUser",this.userInfo).then((res)=>{
						if(res.resultCode==1007){
							let _token={
								value:res.data.ticket,
								time:new Date().getTime(),
								userType:this.userInfo.type
							}
							uni.setStorageSync('token', JSON.stringify(_token));
							uni.showToast({title:res.resultMsg?res.resultMsg:'操作成功',icon:'none',success:()=>{
								setTimeout(()=>{
									uni.switchTab({
										url:"/pages/personal/info?uesrType="+this.userInfo.type,
									})
								},500)
							}})
						}else{
							this.btnDisabled=false
							uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常，请稍后重试',icon:'none'})
						}
					}).catch((err)=>{
						this.btnLoading=false
						console.log(err)
					})
				}else{ //修改
					this.request("/user/editUser",this.userInfo).then((res)=>{
						if(res.resultCode==0){
							uni.showToast({title:res.resultMsg?res.resultMsg:'操作成功',icon:'none',success:()=>{
								setTimeout(()=>{
									uni.switchTab({
										url:"/pages/personal/info",
									})
								},500)
							}})
						}else{
							this.btnDisabled=false
							uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常，请稍后重试',icon:'none'})
						}
					}).catch((err)=>{
						this.btnLoading=false
					})
				}
			},
			getLocation(){
				uni.getLocation({
				 type: 'gcj02',
				 success:(res)=>{
				   this.userInfo.lat = res.latitude
				   this.userInfo.lng = res.longitude
					 
				 }
				})
			},
			pageInit(){
				this.getInComeList()
				this.getCareerList()
			},
			getUserInfo(){
				this.request("/user/info").then((res)=>{
					if(res.resultCode==0){
						// this.userInfo=res.data
						var _data=res.data
						this.userInfo={..._data}
						
						// 遍历职业
						this.careerList.forEach((item,index)=>{
							if(item.code==this.userInfo.career){
								this.careerIndex=index
							}
						})
						//遍历收入区间
						this.inComeList.forEach((item,index)=>{
							if(item.code==this.userInfo.incomeLevel){
								this.inComeIndex=index
							}
						})
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			}
		},
		onLoad(params){
			let _channelId=uni.getStorageSync('channelId');
			if(_channelId){
				this.userInfo.channelId=_channelId
			}
			console.log(this.userInfo.channelId)
			this.fromType=params.fromType
			this.pageInit()
			if(params.fromType==2){ //修改
				this.getUserInfo()
				uni.setNavigationBarTitle({
						title:"修改个人信息"
				});
			}else{
				this.userInfo.type=params.userType
				this.userInfo.authId=params.authId
				this.userInfo.mobile=params.mobile
				this.getLocation()
			}
		}
	}
</script>

<style lang="stylus">
	.wrapper
		padding-bottom:200upx
		.form_wrapper
			background:rgba(248,249,252,1);
			border-radius:16upx;
			padding:0upx 30upx
			margin-top:30upx
		.register_btn
			margin-top:50upx
			.lg
				height:112upx
				background-color:#1F2334
				letter-spacing 2upx
				color:#fff
</style>